import { ChangeDetectorRef, inject, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { BehaviorSubject, Observable, ReplaySubject, Subject, Subscription } from 'rxjs';

type Subscribable<T> = Observable<T> | Subject<T> | BehaviorSubject<T> | ReplaySubject<T>;

@Pipe({ name: 'masonry', standalone: true, pure: false })
export class MasonryPipe<T> implements PipeTransform, OnDestroy {
  private currentObs: Subscribable<T> | null = null;
  private sub: Subscription | null = null;
  latestValue: T | null = null;

  private cdr: ChangeDetectorRef = inject(ChangeDetectorRef);
  private dispose() {
    if (this.sub) {
      this.sub.unsubscribe();
      this.sub = null;
    }
  }

  ngOnDestroy() {
    this.dispose();
    this.currentObs = null;
  }

  private subscribe(obs: Subscribable<T>) {
    this.currentObs = obs;
    this.sub = obs.subscribe({
      next: (res) => {
        this.latestValue = res;
        this.cdr.markForCheck();
      },
      error: (error) => {
        throw error;
      },
    });
  }

  transform(obs: Subscribable<T> | null | undefined, numColumns: number, colNum: number): T | null {
    if (!obs) {
      this.dispose();
      return null;
    }

    if (obs !== this.currentObs) {
      this.dispose();
      this.subscribe(obs);
    }

    if (!Array.isArray(this.latestValue)) {
      return null;
    } else {
      return this.latestValue.filter((_, index) => index % numColumns === colNum - 1) as T;
    }
  }
}
