import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FEED_TYPE } from '@enums/feed-type';

@Component({
  selector: 'app-feed-type-switch',
  templateUrl: 'feed-type-switch.component.html',
  styleUrls: ['feed-type-switch.component.scss'],
  standalone: true,
  imports: [CommonModule],
})
export class FeedTypeSwitchComponent {
  @Input() currentFeedType!: FEED_TYPE;
  @Output() feedTypeChange = new EventEmitter<FEED_TYPE>();

  setFeedType(feedType: FEED_TYPE) {
    this.feedTypeChange.emit(feedType);
  }

  protected readonly FEED_TYPE = FEED_TYPE;
}
