import { Directive, Output, EventEmitter, ElementRef, OnInit, AfterViewInit } from '@angular/core';

@Directive({
  selector: '[appMasonryFeed]',
  standalone: true,
})
export class MasonryFeedDirective implements OnInit, AfterViewInit {
  @Output() fillEmptyPage = new EventEmitter<void>();
  private window!: Window;
  constructor(private el: ElementRef) {}

  ngOnInit() {
    this.window = window;
  }

  ngAfterViewInit() {
    const heightOfElement = this.el.nativeElement.scrollHeight;
    const innerHeight = this.window.innerHeight;
    if (heightOfElement < innerHeight) {
      this.fillEmptyPage.emit();
    }
  }
}
