import { filter, Observable, startWith, Subject, take, takeUntil } from 'rxjs';
import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { CommonModule } from '@angular/common';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { IPostFull, Post } from '@ApiClient/backend/Post/post';

import { ScrollBottomDirective } from '../../directives/scroll-bottom.directive';
import { PostCardComponent } from '@components/post-card/post-card.component';
import { SkeletonCardComponent } from '@components/skeleton-card/skeleton-card.component';
import { selectIsAllUserPostsLoaded, selectIsLoadingProfileFeed, selectUserPosts } from '@store/profile/profile.selectors';
import { AppStateInterface } from '@store/app/app-state.interface';
import { getUserPostsAction } from '@store/profile/profile.actions';
import { FEED_TYPE } from '@enums/feed-type';
import { MasonryPipe } from '../../pipes/masonry.pipe';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { SkeletonModule } from 'primeng/skeleton';
import { MasonryFeedDirective } from '../../directives/masonry-feed.directive';
import { RouterLink } from '@angular/router';
import { map } from 'rxjs/operators';
import { URLS } from '@enums/urls';

@Component({
  selector: 'app-profile-posts-feed',
  templateUrl: 'profile-posts-feed.component.html',
  styleUrls: ['profile-posts-feed.component.scss'],
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  imports: [
    CommonModule,
    PostCardComponent,
    ScrollBottomDirective,
    SkeletonCardComponent,
    TranslateModule,
    MasonryPipe,
    ImageComponent,
    SkeletonModule,
    MasonryFeedDirective,
    RouterLink,
  ],
})
export class ProfilePostsFeedComponent implements OnDestroy, OnInit {
  @Input() userId!: string;
  @Input() isLoading: boolean | null = null;
  @Input() feedType!: FEED_TYPE;

  masonryFeedCount: number = 20;
  masonryColumnNumber = 3;
  masonryColumnArray = [];
  userPosts$: Observable<IPostFull[] | null> = this.store.select(selectUserPosts);
  isLoadingFeed$: Observable<boolean> = this.store.select(selectIsLoadingProfileFeed);
  allPostLoaded$: Observable<boolean> = this.store.select(selectIsAllUserPostsLoaded);
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );
  private ngUnsubscribe = new Subject<void>();

  constructor(
    private store: Store<AppStateInterface>,
    private translate: TranslateService,
  ) {}

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  ngOnInit() {
    this.masonryColumnArray = Array.from({ length: this.masonryColumnNumber });
  }

  onScrollingFinished(postCount: number) {
    if (!this.isLoading) {
      this.allPostLoaded$
        .pipe(
          filter((allPostLoaded) => !allPostLoaded),
          takeUntil(this.ngUnsubscribe),
          take(1),
        )
        .subscribe(() => {
          this.store.dispatch(getUserPostsAction({ count: postCount, userId: this.userId }));
        });
    }
  }

  onMasonryInit() {
    if (!this.isLoading) {
      this.allPostLoaded$
        .pipe(
          filter((allPostLoaded) => !allPostLoaded),
          takeUntil(this.ngUnsubscribe),
          take(1),
        )
        .subscribe(() => {
          this.store.dispatch(getUserPostsAction({ count: this.masonryFeedCount, userId: this.userId }));
        });
    }
  }

  getImageUrl(post: IPostFull): string {
    return Post.getUrlPostFileThumbnail({
      fileId: post.postFiles[0].fileId,
      userCreatorId: post.userCreatorId,
      id: post.postFiles[0].id,
    });
  }

  protected readonly FEED_TYPE = FEED_TYPE;
  protected readonly URLS = URLS;
}
